const state = {

    token:localStorage.getItem("token")
}

const actions = {

}

const mutations = {


    saveUserInfo(state, data){
        state.token = data.token
        localStorage.setItem("token" ,data.token)
    },
    logout(state, data){
        state.token = ""
        localStorage.removeItem("token")
    }
}
export default {
    actions, mutations, state
}