import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/signin',

        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Auth/SignIn.vue')
    },
    {
        path: '/signup',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Auth/SignUp.vue')
    },
    {
        path: '/exerciseSets',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Exercise/Sets.vue')
    },
    {
        path: '/exerciseSets/:id',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Exercise/SetDetail.vue')
    },
    {
        path:"/exerciseSets/:sid/:id",
        component: () => import("../views/Exercise/Code.vue")
    },
    {
       path:"/dashboard",
       redirect:"/dashboard/exerciseSets",
    },
    {
       path:"/active/:token",
       component: () => import("../views/Auth/Active")
    },
    {
        path:"/dashboard/exerciseSets",
        component: () => import("../views/Dashboard/ExerciseSets")
    },
    {
        path:"/editor/exerciseSets/:id",
        component: ()=> import("../views/Editor/ExerciseSet")
    }
]

const router = new VueRouter({
    base: process.env.BASE_URL,
    routes
})

export default router
