<template>
    <header class="navbar">
        <router-link to="/">
            <img class="navbar-logo" src="@/assets/logo.png"/>
        </router-link>


        <div class="navbar-menu">
            <router-link to="/exerciseSets">题库</router-link>
            <router-link to="/about">关于</router-link>
        </div>

        <div v-if="!token">
            <router-link to="/signin" style="margin-right: 20px">登录</router-link>
            <router-link to="/signup">注册</router-link>
        </div>
        <div v-else>
            <el-dropdown @command="command">
                <el-avatar />
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="feedback"><i class="el-icon-mic"/>建议反馈</el-dropdown-item>
                    <el-dropdown-item command="logout"><i class="el-icon-switch-button"/>退出登录</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </header>
</template>

<script>
    export default {
        name: "Navbar",
        computed:{
            token(){
                return this.$store.state.user.token
            }
        },
        methods:{
            command(cmd){
                switch (cmd){
                    case "logout":
                        this.$store.commit("logout")
                }
            }
        }
    }
</script>

<style lang="less" scoped>

    .navbar{
        height: 60px;
        display: flex;
        flex-direction: row;
        align-items: center;
        background-color: white;
        justify-content: space-between;
        padding: 0 20px;
        box-shadow: 0px 5px 20px 0px rgba(0,87,255,0.1);
    }

    .navbar-menu{
        flex: 1;
        padding-left: 20px;
        a{
            margin-left: 20px;
            display: inline-block;
            padding: 0 20px;
        }
    }
    .navbar-logo{
        height: 40px;
    }
</style>